import React, { useState } from 'react';
import { Grid, Typography, Container, Button, Stack } from '@mui/material';
import { MuiOtpInput } from 'mui-one-time-password-input'
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import { useNavigate } from 'react-router-dom';

function OtpVerification() {
    const [value, setValue] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate();

    const handleChange = (newValue) => {
        setValue(newValue);
        setError('');
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        navigate('/main-page');
    };

    return (
        <Container maxWidth="sm">
            <Grid
                container
                spacing={2}
                direction="column"
                alignItems="center"
                justifyContent="center"
            >
                <Grid item display={'flex'} justifyContent={'center'}>
                    <img src="../assests/profile/vijay.jpeg" width={'50%'} height={'50%'} />
                </Grid>
                <Grid item>
                    <Typography variant="h5">  </Typography>
                </Grid>
                <Grid item display={'flex'}>
                    <PhoneAndroidIcon fontSize="large" /> <Typography ml="2" variant="h6" align="center">
                        OTP Verification
                    </Typography>
                </Grid>
                <Grid item>
                    <Typography variant="body2" align="center">
                        You will get a OTP via SMS
                    </Typography>
                </Grid>
                <Grid item>
                    <form onSubmit={handleSubmit}>
                        <Stack mt={4} direction="column" spacing={5} alignItems="center">
                            <MuiOtpInput
                                value={value}
                                onChange={handleChange}
                                variant="outlined"
                                autoFocus
                                required
                            />
                            {error && (
                                <Typography color="error" variant="body2">
                                    {error}
                                </Typography>
                            )}
                            <Button type="submit" variant="contained" color="primary" fullWidth>
                                Verify OTP
                            </Button>
                        </Stack>
                    </form>
                </Grid>
            </Grid>
        </Container>
    );
}

export default OtpVerification;
