import React, { useState } from 'react';
import { Grid, Typography, Container, Button, Stack } from '@mui/material';
import { MuiTelInput } from 'mui-tel-input';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import { useNavigate } from 'react-router-dom';

function PhoneNumberVerification() {
    const [value, setValue] = useState('+91 1234567890');
    const navigate = useNavigate();

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        navigate('/otp-verification');
    };

    return (
        <Container maxWidth="sm">
            <Grid
                container
                spacing={2}
                direction="column"
                alignItems="center"
                justifyContent="center"
            >
                <Grid item display={'flex'} justifyContent={'center'}>
                    <img src="../assests/profile/vijay.jpeg" width={'50%'} height={'50%'} />
                </Grid>
                <Grid item>
                    <Typography variant="h5">  </Typography>
                </Grid>
                <Grid item display={'flex'}>
                    <PhoneAndroidIcon fontSize="large" /> <Typography ml="2" variant="h6" align="center">
                        Verification
                    </Typography>
                </Grid>
                <Grid item>
                    <Typography variant='body2' align="center">
                        We will send you a One Time Password to your phone number
                    </Typography>
                </Grid>
                <Grid item>
                    <form onSubmit={handleSubmit}>
                        <Stack mt={4} direction="column" spacing={4}>
                            <MuiTelInput
                                label="Phone Number"
                                defaultCountry={'IN'}
                                fullWidth
                                variant="outlined"
                                value={value}
                                onChange={handleChange}
                                required
                            />
                            <Button type="submit" variant="contained" color="primary" size='large'>
                                GET OTP
                            </Button>
                        </Stack>
                    </form>
                </Grid>
            </Grid>
        </Container>
    );
}

export default PhoneNumberVerification;
