import React, { useState } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Drawer, Stack, IconButton, Button } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

const cardData = [
    { id: 1, name: 'Virat kohli', image: '../assests/profile/virat.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
    { id: 2, name: 'MS Dhoni', image: '../assests/profile/dhoni.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
    { id: 3, name: 'Rohit sharma', image: '../assests/profile/rohit.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
    { id: 4, name: 'Kl rahul', image: '../assests/profile/rahul.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
    { id: 5, name: 'Ravindra Jadeja', image: '../assests/profile/jadega.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
    { id: 6, name: 'Rishabh Pant', image: '../assests/profile/pant.jpeg', profession: 'Cricket Player', email: '123@gmail.com', phoneNumber: '1234567890' },
];


const MainCard = ({ data, onCardClick }) => {
    return (
        <Card raised
            sx={{
                maxWidth: 280,
                margin: "0 auto",
                padding: "0.1em",
            }} onClick={() => onCardClick(data)}>
            <CardMedia
                component="img"
                height="150"
                image={data.image}
                alt={data.name}
                style={{ width: '100%' }}
            />
            <CardContent>
                <Typography align='center' variant="body1" component="div">
                    {data.name}
                </Typography>
            </CardContent>
        </Card>
    );
};

function MainPage() {
    const [selectedData, setSelectedData] = useState(null);
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);

    const handleCardClick = (data) => {
        setSelectedData(data);
        setIsDrawerOpen(true);
    };

    const handleCloseDrawer = () => {
        setSelectedData(null);
        setIsDrawerOpen(false);
    };

    return (
        <>
            <Grid container spacing={2}>
                <Grid item xs={12} container justifyContent="center" alignItems="center">
                    <Typography variant="h4">  </Typography>
                </Grid>
                <Grid container spacing={4} mx={1} my={2}>
                    {cardData.map((data) => (
                        <Grid item xs={6} sm={6} md={4} key={data.id}>
                            <MainCard data={data} onCardClick={handleCardClick} />
                        </Grid>
                    ))}
                </Grid>
            </Grid>
            <Drawer
                anchor="bottom"
                open={isDrawerOpen}
                onClose={handleCloseDrawer}
                PaperProps={{
                    sx: { width: "100%" }
                }}>
                <div style={{ width: '100%', height: '100vh' }}>
                    {selectedData && (
                        <Stack alignItems={'center'} spacing={2} sx={{ p: '2rem' }}>
                            <IconButton
                                sx={{ position: 'absolute', top: 0, right: 0 }}
                                onClick={handleCloseDrawer}
                                aria-label="close"
                            >
                                <CloseIcon />
                            </IconButton>
                            <img src={selectedData.image} alt={selectedData.name} style={{ width: '50%' }} />
                            <Typography variant="h6">Name: {selectedData.name}</Typography>
                            <Typography variant="h6">Profession: {selectedData.profession}</Typography>
                            <Typography variant="h6">Email: {selectedData.email}</Typography>
                            <Typography variant="h6">Number: {selectedData.phoneNumber}</Typography>
                            <Button variant='contained'
                                onClick={handleCloseDrawer}>
                                Select as my candidate
                            </Button>
                        </Stack>
                    )}
                </div>
            </Drawer>
        </>
    );
}

export default MainPage;
